import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { resultValue } from '../dataVal';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  private block: string[] = ['월급', '연봉'];
  private userInput: resultValue = {
    salary: 100,
    payday: 10,
    payTerm: this.block[0],
  };

  constructor(public navCtrl: NavController) {
  }

  ionViewDidLoad() {
    document.getElementById('pay0').style.backgroundColor = '#ccc';
    this.userInput.payTerm = this.block[0];
  }

  setPayTerm(val: number) {
    console.log('log:', val);
    document.getElementById('pay' + val).style.backgroundColor = '#ccc';
    document.getElementById('pay' + (val + 1) % 2).style.backgroundColor = '#fff';
    this.userInput.payTerm = this.block[val];
  }

  gotoPage(page: string) {
    if (this.userInput.payday <= 0)
      this.userInput.payday = 1;
    if (this.userInput.payday > 31)
      this.userInput.payday = 31;
    this.navCtrl.push(page, {
      data: this.userInput
    });
  }
}



